body {
  font-family: "Amatic SC", cursive;
}
.container {
  text-align: center;
  margin-top: 40px;
}
.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 60px;
  font-weight: bold;
  color: #2f794d;
  margin-bottom: 10px;
}
.header__buckle {
  font-size: 100px;
}
.btn {
  background-color: #2f794d;
  padding: 5px 10px;
  border: 1px solid #000;
  padding: 5px 10px;
  color: #fff;
  cursor: pointer;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  font-family: "Amatic SC", cursive;
  font-size: 20px;
}
.btn-delete {
  background-color: transparent;
  border: none;
  color: #8f8f8f;
  cursor: pointer;
  font-size: 14px;
  padding: 10px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 0px;
}
.btn-delete:hover {
  font-weight: bold;
}
.textInput {
  border: 1px solid #000;
  padding: 7px 14px 9px;
  padding-top: 10px;
  margin-left: 5px;
}
.notes__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}
.note {
  background: #eae672;
  -webkit-box-shadow: 0 10px 10px 2px rgba(0, 0, 0, 0.3);
          box-shadow: 0 10px 10px 2px rgba(0, 0, 0, 0.3);
  color: #333;
  font-size: 30px;
  font-weight: normal;
  width: 240px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 30px;
  padding: 20px 25px;
  position: relative;
}
.note:nth-child(even) {
  -webkit-transform: rotate(-2deg);
      -ms-transform: rotate(-2deg);
          transform: rotate(-2deg);
}
.note:nth-child(odd) {
  -webkit-transform: rotate(4deg);
      -ms-transform: rotate(4deg);
          transform: rotate(4deg);
}
.pin {
  background-color: #aaa;
  display: block;
  height: 32px;
  width: 2px;
  position: absolute;
  left: 50%;
  top: -16px;
  z-index: 1;
}
.pin:after {
  background-color: #a31;
  background-image: -webkit-radial-gradient(
    25% 25%,
    circle,
    rgba(255, 255, 255, 0.3),
    rgba(0, 0, 0, 0.3)
  );
  background-image: -o-radial-gradient(
    25% 25%,
    circle,
    rgba(255, 255, 255, 0.3),
    rgba(0, 0, 0, 0.3)
  );
  background-image: radial-gradient(
    circle at 25% 25%,
    rgba(255, 255, 255, 0.3),
    rgba(0, 0, 0, 0.3)
  );
  border-radius: 50%;
  -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1),
    inset 3px 3px 3px rgba(255, 255, 255, 0.2),
    inset -3px -3px 3px rgba(0, 0, 0, 0.2), 23px 20px 3px rgba(0, 0, 0, 0.15);
          box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1),
    inset 3px 3px 3px rgba(255, 255, 255, 0.2),
    inset -3px -3px 3px rgba(0, 0, 0, 0.2), 23px 20px 3px rgba(0, 0, 0, 0.15);
  content: "";
  height: 12px;
  left: -5px;
  position: absolute;
  top: -10px;
  width: 12px;
}
.pin:before {
  background-color: rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0 0 0.25em rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 0.25em rgba(0, 0, 0, 0.1);
  content: "";
  height: 24px;
  width: 2px;
  left: 12px;
  position: absolute;
  top: 12px;
  -webkit-transform: rotate(57.5deg);
      -ms-transform: rotate(57.5deg);
          transform: rotate(57.5deg);
}
.error {
  font-size: 30px;
  font-weight: bold;
  color: #e23537;
}



